@import url(https://fonts.googleapis.com/css2?family=Sacramento&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Sacramento&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@400;700;800&display=swap);
/* Navigation */
.navbar {
    margin: 10px auto;
}

.nav-link {
    color: #3b4961 !important;
    font-size: 18px !important;
}

.navbar-brand h1 {
    color: #3b4961;
    margin: 0;
}

.nav-items {
    z-index: 99999;
}

.nav-phone {
    background-color: #3b4961;
    color: #fafafa !important;
    border-radius: 20px;
}

a.nav-phone:hover {
    background-color: #2e5fd2;
    color: #fafafa !important;
}

.nav-link:hover {
    color: #3b4961 !important;
    font-weight: bold !important;
}

@media (max-width: 992px) { 
    .nav-items {
        text-align: center;
        padding-top: 30px;
    }
    .nav-link {
        font-weight: bold;
        padding: 5px !important;
        font-size: 12px !important;
    }
}
/* HEADER */
div.home {
    display: block;
    background: rgb(251,236,236);
    background: linear-gradient(320deg, rgba(251,236,236,1) 14%, rgba(171,196,213,1) 94%);
    height: 100vh !important;
}

h2 {
    font-family: GmarketSansMedium !important;
    font-size: 4rem !important;
    z-index: 10000;
    color: #3b4961 !important;
}

.header-top {
    overflow: hidden !important;
    height: 100vh !important;    
}

.header {
    margin: 10% auto !important;
}

.home-type {
    color: #2e5fd2 !important;
}

.home-building {
    position: absolute;
    top: 50%;
    width: 110%;
}

.home-search {
    background-color: #2e5fd2;
    width: 80% !important;
    margin: 20% 0;
}

.home-circle {
    position: fixed;
    float: right;
    right: 30px;
    bottom: 30px;
    z-index: 9999999;
}

.home-circle img {
    width: 100px;
    transition: all .3s ease-in-out;
    padding-bottom: 10%;
    -webkit-filter: drop-shadow(3px 3px 6px #8e8e8b);
            filter: drop-shadow(3px 3px 6px #8e8e8b);
}

.home-circle img:hover {
    transform: scale(1.05);
}

input.form-control {
    border-radius: 20px 0 0 20px !important;
}

.home-search {
    border-radius: 20px !important;
}

.search-btn {
    color: #fafafa !important;
}

@media (max-width: 1200px) {
    .home-building {
        top: 75%;
        width: 120%;
    }    
}

@media (max-width: 992px) {
    .home-search {
        width: 120% !important;
        z-index: 99999;
    }
    .home-building {
        top: -100px !important;
        left: 20%;
        width: 90%;
    }    
}

@media (max-width: 768px) {
    .home {
        height: 100% !important;
    }
    .home-circle {
        visibility: hidden;
    }
    .home-search {
        width: 100% !important;
        z-index: 999;
    }
    .home-search .form-control,
    .home-search .btn {
        font-size: 18px !important;
    }
    .home-circle {
        z-index: 9999;
    }
    .home-building {
        left: 0 !important;
        top: -200px;
        width: 130%;
    }    
}

@media (max-width: 576px) {
    div.home {
        height: 70vh;
    }
    .header-top {
        height: 70vh;
    }
    .home-building {
        top: -300px;
        width: 130%;
    }    
}

@media (max-width: 380px) {
    .home-building {
        top: 80% !important;
        width: 130%;
    }    
}
/* LOADING */
.loading-container {
    text-align: center;
    width: 100%;
    height: 100%;
    margin: 20% auto;
}

.loading-container h3 {
    color: #0275d8 !important;
    margin-top: 10%;
    font-size: 36px !important;
}

.loading {
    width: 100px !important;
    height: 100px !important;
}
/* PRODUCT RECOMMENDATION */
.product-recom {
    text-align: center !important;
    cursor: pointer;
    background-color: #fafafa !important;
    margin: 10% auto !important;
    margin-bottom: 0% !important;
}

.product-recom-container {
    padding: 0 !important;
    margin: 0 !important;
}

.product-recom-title {
    margin-bottom: 5%;
    font-size: 50px;
}

.product-recom-nav span {
    padding: 0 10px !important;
    font-size: 18px;
}

.product-recom-item {
    width: 100%;
    -webkit-filter: drop-shadow(3px 3px 6px #8e8e8b) !important;
            filter: drop-shadow(3px 3px 6px #8e8e8b) !important;
    margin-bottom: 3% !important;
}

.product-recom-item h4 {
    text-align: left;
    background-color: #2e5fd2;
    color: #fafafa;
    margin: 0;
    padding: 15px;
    height: 90px;
    font-size: 22px !important;
}

.product-action {
    position: absolute;
    background-color: #fafafa;
    color: #2e5fd2;
    border: 2px solid #2e5fd2;
    padding: 0 2px;
    left: 30px;
    top: 100px;
}

.product-id {
    font-size: 18px;
    margin-right: 5px;
    padding: 3px;
}

.product-recom-item img {
    background: #fafafa;
    width: 100%;
    height: 300px;
    margin: 0;
}

.product-recom .detail-more {
    text-decoration: none !important;
    color: #fafafa !important;
    background-color: #2e5fd2;
    position: relative;
    top: 0px !important;
    padding: 10px 30px;
    border-radius: 20px;
}

.detail-more:hover {
    background-color: #3b4961;
}

.product-recom-list {
    background-color: #fafafa !important;
    padding: 50px 0;
    margin: 0 !important;
}

.product-recom .btn {
    text-align: center !important;
    font-size: 20px !important;
    width: 300px !important;
}

.product-recom .load__btn {
    color: #fafafa;
    background-color: #2e5fd2;
    margin: 0 auto;
    margin-top: 5% !important;
    margin-bottom: 10%;
    border-radius: 20px;
}

.product-recom .load__btn:hover {
    color: #fafafa;
    background-color: #3b4961;
}

.product-won {
    font-size: 26px;
    font-weight: bold;
}

.product-location {
    font-size: 17px;
}

.product-location svg {
    margin-right: 5px;
}

.product-sold {
    position: relative;
    background-color: transparent !important;
    top: 20% !important;
    height: 350px !important;
}

.product-detail {
    border-top: 1px solid #3b4961;
    padding:15px 0;
    width: 85%;
    height: 180px;
    margin: 0 auto;
    font-size: 18px;
}

.product-detail p {
    text-align: left;
    margin: 0 auto;
    margin-top: 10px;
    height: 40px;
    margin-bottom: 0;
}

.product-detail span {
    width: 50%;
}

.product-id {
    background-color: #fafafa;
    color: #3b4961;
}

@media (max-width: 992px) {
    .product-recom .product-recom-list {
        margin-bottom: 5% !important;
    }
}

@media (max-width: 768px) {
    .product-recom {
        display: block;
        margin-top: 30% !important;
    }
    .product-recom-item {
        margin-bottom: 10%;
    }
    .product-recom .load__btn {
        margin-top: 10%;
        margin-bottom: 30%;
    }
}
/* PRODUCT CARDS */
.product-list {
    text-align: center !important;
    background-color: #fafafa !important;
    margin: 10% auto !important;
    margin-bottom: 0% !important;
}

.product-title {
    font-size: 50px;
    margin-bottom: 5%;
}

.top-row {
    margin-bottom: 5% !important;
}

.product-item {
    cursor: pointer;
    width: 100%;
    height: 100%;
    transition: all .3s ease-in-out;
    -webkit-filter: drop-shadow(3px 3px 6px #8e8e8b);
            filter: drop-shadow(3px 3px 6px #8e8e8b);
